#Nav{
    min-height: 60px;
    width: 100%;
    max-width: 65rem;
    margin: 0 auto;
    background: #FFFFFF;
    display: flex;
    align-items: center;
    padding: 10px;

    & .container-nav{
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;
    

        & .logo{
            a{
                color: #000;
                text-decoration: none;
                font-size: 2rem;
                font-weight: bold;
                font-family: 'Roboto', sans-serif;
            }
        }
    }
}