#RepeaterZipCodes{
    padding: 20px;
    padding-bottom: 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-end;

    & .content{
       flex : 70 0;
    }


    & p {
        margin-bottom: 10px;
        font-size: 18px;
        text-transform: uppercase;
    }

    & .inputs {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
        grid-gap: 20px;

        & .errors-border {
            border: 1px solid red;
        }

        & .error {
            color: red;
            font-size: 12px;
            margin-top: 5px;
        }

        & div {
            width: 100%;

            & input{
                width: 100%;
                border: none;
                border-radius: 4px;
                border : 1px solid #ccc;
                background-color: #fff;
                padding: 5px;
                font-size: 1.2rem;
                color: #000;
                font-weight: bold;
                text-align: center;
            }
        }
    }

    .button {
        width: 150px;
        border: none;
        border-radius: 4px;
        border : 1px solid #ccc;
        background-color: #000;
        color: #fff;
        padding: 5px;
        font-size: 1.2rem;
        text-align: center;
        height: 35px;
        cursor: pointer;
    
        &:hover {
            background-color: #fff;
            color: #000;
            transition: all 0.3s ease-in-out;
        }
    }
}